<template>  
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="80px">
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getInit"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="addDialog=true"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="shopName" label=""></el-table-column>
      <el-table-column prop="manager" label=""></el-table-column>
      <el-table-column prop="" label="">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="" label="">
        <template slot-scope="scope" v-if="scope.row.gmtModified">
          {{$root.getDateArray(scope.row.gmtModified)[9]}}
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=250>
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
					<el-button type="primary" size="small" @click="details(scope.row)"></el-button>
        </template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formPush" ref='addList'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-select @change="changeShopId" v-model="shopId" placeholder="">
              <el-option v-for="(item,index) in lishopinfo" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small" prop="shopId">
            <el-select v-model="formPush.shopId" placeholder="">
              <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>

        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formUpdate.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-select @change="changeShopId" v-model="shopId" placeholder="">
              <el-option v-for="(item,index) in lishopinfo" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small" prop="shopId">
            <el-select v-model="formUpdate.shopId" placeholder="">
              <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="updateList('formUpdate')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="detailsDialog">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small" prop="shopName">
            <el-input v-model="formUpdate.shopName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-upload
              :disabled="true"
              class="avatar-uploader"
              :action="uploadToRealPath"
              :show-file-list="false">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="" size="small" prop="phone">
            <el-input v-model="formUpdate.phone"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="address">
            <el-input v-model="formUpdate.address"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-select v-model="formUpdate.genreId" placeholder="" prop="genreId">
              <el-option label="" :value="''"></el-option>
              <el-option v-for="(item,index) in lishopinfo" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.distance" placeholder="km"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.intro"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="adesc">
            <el-input type="textarea" v-model="formUpdate.remark" :rows="2" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="picture">
            <el-upload
              ref="Updata"
              :disabled="true"
              :action="uploadToRealPath"
              list-type="picture-card"
              :file-list="fileList">
              <i class="el-icon-plus"></i>
            </el-upload> 
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="detailsDialog = false"> </el-button>
      </div>
    </el-dialog>
    </section>
</template>

<script>
import paging from "../paging"
import {getgenreid,shopgetdeaie,getlishopinfo,pmtypePhone,ShopDataList,addrecommList,updatecommendList,deleteRecommend,xqSelectList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      fileList:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        current:1,
        size:10
      },
      applyInfos:{},
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      detailsDialog:false,
      pictrueNum:0,
      formPush:{},
      uploadToRealPath:'/intellmanagerV3.0/upload/file/upload',
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      rules: {
        shopName:[{ required: true, message: ''}],
        address:[{ required: true, message: ''}],
        phone:[{ required: true, message: ''}],
        genreId:[{ required: true, message: '',trigger: 'change'}],
        xqId:[{ required: true, message: '',trigger: 'change'}],
        shopId:[{ required: true, message: '',trigger: 'blur'}],
      },
      lishopinfo:[],
      shopId:'',
      imageUrl:'',
      genreid:[]
    }
  },
  methods:{
    details(item){//
      let params = {
        id:item.shopId
      }
      shopgetdeaie(params).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
      this.fileList= []
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
        shopName:item.shopName,
        remark:item.remark,
        phone:item.phone,
        intro:item.intro,
        distance:item.distance,
        genreId:item.id,
        address:item.address,
        picture:item.picture,
        shopImage:item.shopImage,
      }
      this.imageUrl = item.shopImage
      if(item.picture){
        item.picture.split(',').forEach(item=>{
          let obj = {
            url:item
          }
          if(item){
            this.fileList.push(obj)
          }
          
        })
        
      }
        this.detailsDialog = true
        this.updateShowBox(res.data.data)
        }
      })
    },
    changeShopId(val){
      this.formPush.shopId = ''
      this.formUpdate.shopId = ''
      console.log(val)
      let params = {
        genreId:val
      }
      getgenreid(params).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.genreid = res.data.data
          if(res.data.data){
            this.formPush.shopId = res.data.data[0].id
            // this.formUpdate.shopId = res.data.data[0].id
          }

        }
      })
    },
    getlist(){
      ShopDataList(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      this.getlist()
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          if(this.xqTree.length!=0){
            this.formSearch.xqId = this.xqTree[0].id
          }
        }
      })
      getlishopinfo({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.lishopinfo = res.data.data
        }
      })
      getgenreid({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.genreid= res.data.data
        }
      })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addrecommList(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = this.formUpdate
          updatecommendList(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getlist()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
        xqId:item.xqId,
        shopId:parseInt(item.shopId)
      }
      let params = {
        genreId:item.id
      }
      getgenreid(params).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.genreid = res.data.data
        }
      })
      // this.changeShopId(item.shopId)
      this.shopId = item.id
      this.updateDialog = true
      
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        deleteRecommend(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>

</style>



