<template>  
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="80px">
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.status" placeholder="">
          <el-option label="" :value="0"></el-option>
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
          <el-option label="" :value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.creator" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.nickname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.remark" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getlist"> </el-button>
      </el-form-item>
      <!-- <el-form-item>
        <el-button size="small" @click="addDialog=true"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item> -->
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="linkmanName" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column prop="intro" label=""></el-table-column>
      <el-table-column prop="shopName" label=""></el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="cname" label=""></el-table-column>
      <el-table-column prop="classify" label="">
        <template slot-scope="scope">
          {{scope.row.status==1?"":scope.row.status==2?"":scope.row.status==3?"":""}}
        </template>
      </el-table-column>
      
      <el-table-column prop="" label="" width="150">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="150">
        <template slot-scope="scope" v-if="scope.row.gmtModified">
          {{$root.getDateArray(scope.row.gmtModified)[9]}}
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=200>
				<template slot-scope="scope">
					<el-button type="warning" v-if="scope.row.status==0" size="small" @click="updexpressinfo(scope.row,1)"> </el-button>
					<el-button type="warning" v-if="scope.row.status==1" size="small" @click="updexpressinfo(scope.row,2)"></el-button>
        </template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formPush" ref='addList'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small" prop="goodsName">
            <el-input v-model="formPush.goodsName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="fkShopId">
            <el-select v-model="formPush.fkShopId" placeholder="">
              <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small" prop="goodsName">
            <el-input v-model="formUpdate.goodsName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="fkShopId">
            <el-select v-model="formPush.fkShopId" placeholder="">
              <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="updateList('formUpdate')"> </el-button>
      </div>
    </el-dialog>
    </section>
</template>

<script>
import paging from "../paging"
import {getgenreid,getlishopinfo,getsmallSmaItempress,updexpressinfo,xqSelectList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      fileList:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        current:1,
        size:10
      },
      applyInfos:{},
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      detailsDialog:false,
      pictrueNum:0,
      formPush:{},
      uploadToRealPath:'/intellmanagerV3.0/upload/file/upload',
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      rules: {
        goodsName:[{ required: true, message: ''}],
        address:[{ required: true, message: ''}],
        phone:[{ required: true, message: ''}],
        fkShopId:[{ required: true, message: '',trigger: 'change'}],
        genreId:[{ required: true, message: '',trigger: 'change'}],
        xqId:[{ required: true, message: '',trigger: 'change'}],
        shopId:[{ required: true, message: '',trigger: 'change'}],
      },
      lishopinfo:[],
      shopId:'',
      imageUrl:'',
      genreid:[]
    }
  },
  methods:{
    updexpressinfo(item,status){// (/)
      let params={
        id:item.id,
        status:status,
        xqId:item.xqId,
      }
      updexpressinfo(params).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.getlist()
          this.$message({
            message: '',
            type: 'success'
          });
          
        }else{
          this.$message(':'+res.data.msg)
        }
      })
    },
    getlist(){
      getsmallSmaItempress(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      this.getlist()
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          if(this.xqTree.length!=0){
            this.formSearch.xqId = this.xqTree[0].id
          }
        }
      })
      // getlishopinfo({}).then((res)=>{//
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.lishopinfo = res.data.data
      //   }
      // })
      // let getgenreidp = {
      //   genreId:1
      // }
      // getgenreid(getgenreidp).then((res)=>{//
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.genreid= res.data.data
      //     this.formPush.fkShopId = res.data.data[0].id
      //   }
      // })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addgetsmallSmaItempress(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = this.formUpdate
          updategetsmallSmaItempress(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getlist()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
        xqId:item.xqId,
        goodsName:item.goodsName,
        fkShopId:item.fkShopId
      }

      this.updateDialog = true
      
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        deleletgetsmallSmaItempress(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>

</style>



