<template>
  <section>
    <el-form :inline="true" :model="formSearch" class="form_inline">
      <el-form-item label="" size="small">
       <el-cascader
          ref="cascader"
          v-model="option1"
          :options="dataTree"
          :props="defaultProps2"
          @change="handleChange"
          clearable
         >
        </el-cascader>
      </el-form-item>
    </el-form>
    <el-form-item label="" size="small">
      <el-input v-model="formSearch.name" placeholder=""></el-input>
    </el-form-item>

    <el-form :inline="true" :model="formSearch">


      <el-form-item label=":" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label=":" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>


      <el-form-item label=":" size="small">
       <el-cascader
          ref="cascader"
          v-model="option1"
          :options="dataTree"
          :props="defaultProps2"
          @change="handleChange"
          clearable
         >
        </el-cascader>
      </el-form-item>
  <el-form-item label=":" size="small">
       <el-cascader
          ref="cascader"
          v-model="option1"
          :options="dataTree"
          :props="defaultProps2"
          @change="handleChange"
          clearable
         >
        </el-cascader>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="getInit"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="addDialog=true"> </el-button>
      </el-form-item>
    </el-form>
    <!-- <el-table :data="formData" style="width: 100%" stripe>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="orgName" label=""></el-table-column>
      <el-table-column prop="creator" label="" width="180"></el-table-column>
      <el-table-column prop="gmtCreate"label=""width="180"></el-table-column>
      <el-table-column label="" fixed="right" width=250>
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
				</template>
			</el-table-column>
    </el-table> -->
    <el-table :data="formData" style="width: 100%" stripe>
      <el-table-column prop="item1" label=""></el-table-column>
      <el-table-column prop="item2" label=""></el-table-column>
      <el-table-column prop="item3" label=""></el-table-column>
      <el-table-column prop="item4"label=""></el-table-column>
      <el-table-column prop="item5"label=""></el-table-column>
      <el-table-column prop="item6"label=""></el-table-column>
      <el-table-column prop="item7"label=""></el-table-column>

      <el-table-column label="" fixed="right" width=400>
				<template slot-scope="scope">
					<el-button type="" size="small" @click="deleInfor(scope.row.id)"></el-button>
					<el-button type="" size="small" @click="deleInfor(scope.row.id)"></el-button>
          <el-button type="" size="small" @click="deleInfor(scope.row.id)"></el-button>
					<el-button type="" size="small" @click="deleInfor(scope.row.id)"></el-button>
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
  </section>
</template>

<script>
import paging from "../paging"
// import { xqList,xqAddList,xqUpdateList,xqDeleteList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      option1:[],
      formSearch:{//
        current:1,
        size: 10
      },
      formPush:{},//
      formData: [
        {
          item1:"",
          item2:"18002530462",
          item3:"",
          item4:"2020-07-13 15:20:15	",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {	
          item1:"",
          item2:"17621016392",
          item3:"",
          item4:"2019-06-04 18:08:37",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {
          item1:"",
          item2:"18002530462",
          item3:"",
          item4:"2019-02-15 16:23:33",
          item5:"admin",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {
          item1:"",
          item2:"18002530462",
          item3:"",
          item4:"2020-07-13 15:20:15	",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {
          item1:"",
          item2:"18898504869",
          item3:"okkkk",
          item4:"2020-07-13 15:20:15	",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {
          item1:"",
          item2:"18002530462",
          item3:"",
          item4:"2020-07-13 15:20:15	",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },
        {
          item1:"",
          item2:"18002530462",
          item3:"",
          item4:"2020-07-13 15:20:15	",
          item5:"adminky",
          item6:"2020-07-22 18:22:05",
          item7:"",
          item8:"",

        },

      ],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dataTree:[],
      defaultProps: {//key
        children: 'children',
        label: 'label'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
    }
  },
  methods:{
    getInit(){//
      xqList(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            if(item.gmtCreate){
              item.gmtCreate = this.$root.getDateArray(item.gmtCreate)[9]
            }
            return item
          })
          this.total = res.data.data.total
          this.pages = res.data.pages
        }else{
          this.$message(res.data.msg);
        }
      })
      let org_tree={
            label:'',
            status:1
          }
      orgTree(org_tree).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    addList(){//
      if(!this.formPush.orgId){
        this.$message('');
        return;
      }
      if(!this.formPush.name){
        this.$message('');
        return;
      }
      xqAddList(this.formPush).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message({
            message: '',
            type: 'success'
          });
          this.getInit()
        }else{
          this.$message('')
        }
        this.addDialog = false
      })
    },
    updateList(){//
    console.log(this.formUpdate)
      if(!this.formUpdate.orgId){
        this.$message('');
        return;
      }
      if(!this.formUpdate.id){
        this.$message('');
        return;
      }
      xqUpdateList(this.formUpdate).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message({
            message: '',
            type: 'warning'
          });
          this.getInit()
        }
      })
      this.updateDialog = false
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }
      this.$confirm('')
      .then(_ => {
        xqDeleteList(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getInit()
    },
    checkTreeInfor(data,ev) {//
      // console.log(ev);
      if(ev.checkedKeys.length>1){
        this.$message('');
      }else{
        this.formPush.orgId = ev.checkedKeys[0]
        this.formUpdate.orgId = ev.checkedKeys[0]
      }
    },
    updateShowBox(item){//
      this.formUpdate = {}
      this.updateDialog = true
      this.formUpdate.orgId = [item.orgId];
      this.formUpdate.id = item.id;
      console.log(this.formUpdate)
    },
    handleChange(value){
      if(value.length!=0){
        console.log(value)
        this.formSearch.orgId=this.$refs.cascader.getCheckedNodes()[0].data.id
      }else{
        this.formSearch.orgId = ''
      }
      xqList(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            if(item.gmtCreate){
              item.gmtCreate = this.$root.getDateArray(item.gmtCreate)[9]
            }
            return item
          })
          this.total = res.data.data.total
          this.pages = res.data.pages
        }else{
          this.$message(res.data.msg);
        }
      })
    }
  },
  mounted(){
    // this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>



