<template>
  <section>
    <div class="pagination">
      <span class="total">
        <span> {{total}} </span>
        <span style="margin-left:20px;" v-if="moduleData.name=='parking_keytop_record'">  {{moduleData.allMoney}}</span>
        <span style="margin-left:20px;" v-if="moduleData.name=='parking_keytop_info'">  {{moduleData.freeSpaceNum}} {{moduleData.totalSpaceNum}}</span>
      </span>
      <el-pagination
        layout="prev, pager, next"
        prev-text=""
        next-text=""
        @current-change="handleCurrentPage"
        :page-size="10" 
        :total="total">
      </el-pagination>
    </div>
  </section>
</template>

<script>
export default {
  props:['getTotal','moduleInfor'],
  data(){
    return{
      total:0,
      money:0,
      moduleData:{},
    }
  },
  watch:{
    getTotal(val){
      console.log(val)
      this.total = val
    },
    moduleInfor(val){
      console.log(val)
      this.moduleData = val
    },
  },
  methods:{
    handleCurrentPage(val){//
      this.$emit('changePage',val)
    },
  }
}
</script>

<style>

</style>


