<template>
  <section class="modlude">
    <el-form :inline="true" :model="formSearch">
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.shopId" placeholder="">
          <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.nickname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.content" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-date-picker
          v-model="formSearch.leaveTime"
          type="datetime"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="getlist"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form>					
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="nickname" label=""></el-table-column>
      <el-table-column prop="phone"label=""></el-table-column>
      <el-table-column prop="content"label=""></el-table-column>
      <el-table-column prop="rating"label=""></el-table-column>
      <el-table-column prop="shopName"label=""></el-table-column>
      <el-table-column prop=""label="">
        <template slot-scope="scope" v-if="scope.row.leaveTime">
          {{$root.getDateArray(scope.row.leaveTime)[9]}}
        </template>
      </el-table-column>

      <el-table-column label="" fixed="right" width=250>
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
					<el-button size="small" @click="updateShowBox(scope.row)"></el-button>
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_left">
        <el-tree
          :data="dataTree"
          check-strictly
          show-checkbox
          node-key="id"
          default-expand-all
          @check="checkTreeInfor"
          :props="defaultProps"
          ref="tree">
        </el-tree>
      </div>
      <div class="cont_box_right">
        <el-form label-position="right" label-width="80px" :model="formPush" ref='formPush'>
          <el-form-item label="">
            <el-input v-model="formPush.name"></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="90px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.nickname"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.phone"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.content"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.fromSystemjyh"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="$root.getDateArray(formUpdate.leaveTime)[9]"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.rating"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.shopName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-image style="width: 100px; height: 100px" :src="formUpdate.picture" fit="contain"></el-image>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import paging from '../paging'
import { getgenreid ,shopMessageList,addShoptype,updateShoptype,deleteShopMessage,orgTree,xqSelectList,houseList } from '../../url/api';
export default {
  data(){
    return{
      option1:[],
      deleBatch:[],
      isGetFather:false,//
      formSearch:{//
        classify:8,
        current:1,
        size: 10
      },
      formPush:{
      },//
      formData: [],//
      formUpdate:{
      },//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dataTree:[],
      genreid:[],
      defaultProps: {//key
        children: 'children',
        label: 'label'
      },
      xqTree:[],//
      rules: {
        name:[{ required: true, message: ''}],
        ubicellJyh:[{ required: true, message: ''}],
        addUser:[{ required: true, message: '',trigger: 'change'}],
        getCall:[{ required: true, message: '',trigger: 'change'}],
        xqId:[{ required: true, message: '',trigger: 'change'}],
        classify:[{ required: true, message: '',trigger: 'change'}],
      },
    }
  },
  methods:{
    getlist(){
      shopMessageList(this.formSearch).then((res)=>{///
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
          this.pages = res.data.pages
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){//
      let org_tree1={}
      xqSelectList(org_tree1).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          if(this.xqTree.length!=0){
            this.formSearch.xqId = this.xqTree[0].id
            this.getlist()
          }
        }
      })
      let getgenreidp = {
        genreId:4
      }
      getgenreid(getgenreidp).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.genreid= res.data.data
          // this.formPush.formSearch = res.data.data[0].id
        }
      })
    },
    addList(addList){//
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addShoptype(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getInit()
            }else{
              this.$message('')
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(){//
    console.log(this.formUpdate)
      updateShoptype(this.formUpdate).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message({
            message: '',
            type: 'warning'
          });
          this.getInit()
        }
      })
      this.updateDialog = false
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        deleteShopMessage(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getInit()
    },
    checkTreeInfor(data,ev) {//
      // console.log(ev);
      if(ev.checkedKeys.length>1){
        this.$message('');
      }else{
        this.formPush.orgId = ev.checkedKeys[0]
        this.formUpdate.orgId = ev.checkedKeys[0]
      }
    },
    updateShowBox(item){//
      this.formUpdate = {
        id:item.id,
        content:item.content,
        fromSystemjyh:item.fromSystemjyh,
        leaveTime:item.leaveTime,
        nickname:item.nickname,
        phone:item.phone,
        picture:item.picture,
        rating:item.rating,
        shopName:item.shopName,
      }
      this.updateDialog = true
      console.log(this.formUpdate)
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>



