<template>
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline">
      <!-- <el-form-item label="" size="small">
        <el-select v-model="formSearch.classify" placeholder="">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
          <el-option label="" :value="3"></el-option>
          <el-option label="" :value="4"></el-option>
          <el-option label="" :value="5"></el-option>
          <el-option label="" :value="6"></el-option>
          <el-option label="" :value="7"></el-option>
          <el-option label="" :value="8"></el-option>
        </el-select>
      </el-form-item> -->
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-date-picker
          v-model="formSearch.gmtCreate"
          type="datetime"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getInit"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="addDialog=true"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form>	
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="cname" label=""></el-table-column>
      <el-table-column prop=""label="">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
     
      <el-table-column label="" fixed="right" width=250>
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"> </el-button>
				
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"></el-button>
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" label-width="80px" :model="formPush" ref='formPush' :rules="rules">
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="" size="small" prop="classify">
            <el-select v-model="formPush.classify" placeholder="">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
              <el-option label="" :value="4"></el-option>
              <el-option label="" :value="5"></el-option>
              <el-option label="" :value="6"></el-option>
              <el-option label="" :value="7"></el-option>
              <el-option label="" :value="8"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="name">
            <el-input v-model="formPush.name"></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('formPush')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" label-width="80px" :model="formUpdate" ref='formUpdate' :rules="rules">
          <!-- <el-form-item label="" size="small" prop="classify">
            <el-select v-model="formUpdate.classify" placeholder="">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
              <el-option label="" :value="4"></el-option>
              <el-option label="" :value="5"></el-option>
              <el-option label="" :value="6"></el-option>
              <el-option label="" :value="7"></el-option>
              <el-option label="" :value="8"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="name">
            <el-input v-model="formUpdate.name"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium" @click="updateDialog = false"> </el-button>
        <el-button size="medium" @click="updateList('formUpdate')"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import paging from '../paging'
import { shoptypeList,addShoptype,updateShoptype,deleteShoptype,orgTree,xqSelectList,houseList } from '../../url/api';
export default {
  data(){
    return{
      option1:[],
      deleBatch:[],
      isGetFather:false,//
      formSearch:{//
        classify:8,
        current:1,
        size: 10
      },
      formPush:{
        classify:8
      },//
      formData: [],//
      formUpdate:{
        classify:8
      },//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dataTree:[],
      defaultProps: {//key
        children: 'children',
        label: 'label'
      },
      xqTree:[],//
      rules: {
        name:[{ required: true, message: ''}],
        ubicellJyh:[{ required: true, message: ''}],
        addUser:[{ required: true, message: '',trigger: 'change'}],
        getCall:[{ required: true, message: '',trigger: 'change'}],
        xqId:[{ required: true, message: '',trigger: 'change'}],
        classify:[{ required: true, message: '',trigger: 'change'}],
      },
    }
  },
  methods:{
    getlist(){
      shoptypeList(this.formSearch).then((res)=>{///
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
          this.pages = res.data.pages
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){//
      let org_tree={
            name:'',
            status:1
          }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
      let org_tree1={}
      xqSelectList(org_tree1).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          if(this.xqTree.length!=0){
            this.formSearch.xqId = this.xqTree[0].id
            this.getlist()
          }
        }
      })
    },
    addList(addList){//
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addShoptype(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getInit()
            }else{
              this.$message('')
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(){//
    console.log(this.formUpdate)
      updateShoptype(this.formUpdate).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.$message({
            message: '',
            type: 'warning'
          });
          this.getInit()
        }
      })
      this.updateDialog = false
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        deleteShoptype(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getInit()
    },
    checkTreeInfor(data,ev) {//
      // console.log(ev);
      if(ev.checkedKeys.length>1){
        this.$message('');
      }else{
        this.formPush.orgId = ev.checkedKeys[0]
        this.formUpdate.orgId = ev.checkedKeys[0]
      }
    },
    updateShowBox(item){//
      this.formUpdate = {
        classify:parseInt(item.classify),
        name:item.name,
        id:item.id
      }
      this.updateDialog = true
      console.log(this.formUpdate)
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>



