<template>
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="100px">
      <el-form-item label="" size="small" prop="xqId">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.cph" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.type">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-date-picker
          v-model="formSearch.entryTime"
          type="datetime"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-date-picker
          v-model="formSearch.leaveTime"
          type="datetime"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getlist"> </el-button>
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="cph" label=""></el-table-column>
      <el-table-column prop="carparkNo" label=""></el-table-column>
      <el-table-column prop="ysje"label=""></el-table-column>
      <el-table-column prop="sfje"label=""></el-table-column>
      <el-table-column prop="sfGateOut"label=""></el-table-column>
      <el-table-column prop="sfGate"label=""></el-table-column>
      <el-table-column prop=""label="" width=150>>
        <template slot-scope="scope" v-if="scope.row.outTime">
          {{$root.getDateArray(scope.row.outTime)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="outPic"label="">
        <template slot-scope="scope">
          <el-image  style="width:80px;height:80px;" :src="scope.row.outPic" fit="cover"></el-image>
        </template>
      </el-table-column>
      <el-table-column prop="outGateName"label=""></el-table-column>
      <el-table-column prop=""label="" width=150>>
        <template slot-scope="scope" v-if="scope.row.inTime">
          {{$root.getDateArray(scope.row.inTime)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="inPic"label="">
        <template slot-scope="scope">
          <el-image style="width:80px;height:80px;" :src="scope.row.inPic" fit="cover"></el-image>
        </template>
      </el-table-column>
      <el-table-column prop="inGateName"label=""></el-table-column>
      <el-table-column prop=""label="" width=150>
        <template slot-scope="scope" v-if="scope.row.sfTime">
          {{$root.getDateArray(scope.row.sfTime)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop=""label="" width=150>>
        <template slot-scope="scope" v-if="scope.row.createtime">
          {{$root.getDateArray(scope.row.createtime)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop=""label="">
        <template slot-scope="scope">
          {{scope.row.type==0?'':""}}
        </template>
      </el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
    <!--  -->
  </section>  
</template>

<script>
import paging from "../paging"
import { carRecords,carRecordsFee,delCar,addPropertyTypeList,updatePropertyTypeList,addReply,xqSelectList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      moduleInfor:{},
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        current:1,
        size:10
      },
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dialog:false,
      replyType:1,//1 2.
      formPush:{
      },
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      reply:'',
      rules: {
        name:[{ required: true, message: ''}],
        type:[{ required: true, message: '',trigger: 'blur'}],
        xqId:[{ required: true, message: '',trigger: 'blur'}],
      },
      imageUrl:'',
    }
  },
  methods:{
    getlist(){
      carRecords(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
      // carRecordsFee(this.formSearch).then((res)=>{
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.moduleInfor = {
      //       name:'parking_keytop_record',
      //       allMoney:res.data.data
      //     }
      //   }else{
      //     this.$message(res.data.msg);
      //   }
      // })
    },
    getInit(){
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          this.formSearch.xqId = this.xqTree[0].id
          this.getlist()
        }
      })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addPropertyTypeList(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getInit()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.formUpdate.gmtCreate){
            delete this.formUpdate.gmtCreate
          }
          let params = this.formUpdate
          updatePropertyTypeList(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getInit()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
      }
      this.updateDialog = true
      console.log(this.formUpdate)
    },
   deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.carId)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        delCar(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },

    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>


