      let params = {
        costId: costId,
        level: level
      }
      console.log(params)
      itemSelect(params).then(res => {
        console.log(res)
        if(res.data.code == 200){
          if(level == 2){
            this.itemsele2 = []
            this.checkInfor.levelGroup = 0
            this.checkInfor.levelCost = 0
            this.itemsele2 = res.data.data

          }else if(level == 3){
            this.itemsele3 = []
            this.checkInfor.levelCost = 0
            this.itemsele3 = res.data.data
            if(costId == 0){ 
              this.checkInfor.levelCost = 0
