      this.getList()
    },
    selsChange: function (sels) {// 
      this.sels=[]
      console.log(sels)
      let _this=this
      let selss=sels.filter(function (sel) {
        if(sel.isExist!==1){
          let obj = {
            orderNumber: sel.receiptNumber,
            type: sel.type
          }
          _this.sels.push(obj)
            return sel
        }
      })
