<template>  
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="80px">
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.isVerify	" placeholder="">
          <el-option label="" :value="0"></el-option>
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.typeId	" placeholder="">
          <el-option label="" :value="0"></el-option>
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.nickname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.verifier" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getlist"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="nickname" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="verifier	" label=""></el-table-column>
      <el-table-column prop="isVerify" label="">
        <template slot-scope="scope">
          {{scope.row.isVerify==0?"":scope.row.isVerify==1?"":""}}
        </template>
      </el-table-column>
      <el-table-column prop="typeId" label="">
        <template slot-scope="scope">
          {{scope.row.typeId==0?"":scope.row.typeId==1?"":""}}
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="150">
        <template slot-scope="scope" v-if="scope.row.gmtVerify">
          {{$root.getDateArray(scope.row.gmtVerify)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="150">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=400>
				<template slot-scope="scope">
					<el-button  size="small" @click="shenheHouell(scope.row)">{{scope.row.isVerify==1?"":""}}</el-button>
					<el-button  size="small" @click="gopinglun(scope.row.id)"></el-button>
					<el-button  size="small" @click="godianping(scope.row.id)"></el-button>
					<el-button  size="small" @click="updateShowBox(scope.row),detailsDialog=false"></el-button>
        </template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formPush" ref='addList'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small" prop="goodsName">
            <el-input v-model="formPush.goodsName"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="fkShopId">
            <el-select v-model="formPush.fkShopId" placeholder="">
              <el-option v-for="(item,index) in genreid" :label="item.shopName" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.nickname"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.phone"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.title"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.descDetail"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="$root.getDateArray(formUpdate.gmtCreate)[9]"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.verifier"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="$root.getDateArray(formUpdate.gmtVerify)[9]"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.isVerify==0?'':formUpdate.isVerify==1?'':''"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.typeId==0?'':formUpdate.typeId==1?'':''"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-image style="width: 100px; height: 100px" :src="formUpdate.picture" fit="contain"></el-image>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
      </div>
    </el-dialog>
    </section>
</template>

<script>
import paging from "../paging"
import {getgenreid,houelldelectlist,getlishopinfo,shenheHouell,houellList,xqSelectList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      fileList:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        current:1,
        size:10
      },
      applyInfos:{},
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      detailsDialog:false,
      pictrueNum:0,
      formPush:{},
      uploadToRealPath:'/intellmanagerV3.0/upload/file/upload',
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      rules: {
        goodsName:[{ required: true, message: ''}],
        address:[{ required: true, message: ''}],
        phone:[{ required: true, message: ''}],
        fkShopId:[{ required: true, message: '',trigger: 'change'}],
        genreId:[{ required: true, message: '',trigger: 'change'}],
        xqId:[{ required: true, message: '',trigger: 'change'}],
        shopId:[{ required: true, message: '',trigger: 'change'}],
      },
      lishopinfo:[],
      shopId:'',
      imageUrl:'',
      genreid:[]
    }
  },
  methods:{
    gopinglun(id){//
      this.$router.push({
        path:'./fanwuzushao1',
        query:{
          hsId:id
        }
      })
    },
    godianping(id){//
      this.$router.push({
        path:'./fanwuzushao2',
        query:{
          hsId:id
        }
      })
    },
    shenheHouell(item){// 
      let params = {
        id:item.id,
        isVerify:item.isVerify==1?2:1
      }
      shenheHouell(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.getlist()
            this.$message({
              message: '',
              type: 'success'
            });
            
          }else{
            this.$message(':'+res.data.msg)
          }
        })
    },
    getlist(){
      houellList(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      this.getlist()
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          if(this.xqTree.length!=0){
            this.formSearch.xqId = this.xqTree[0].id
          }
        }
      })
      // getlishopinfo({}).then((res)=>{//
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.lishopinfo = res.data.data
      //   }
      // })
      // let getgenreidp = {
      //   genreId:2
      // }
      // getgenreid(getgenreidp).then((res)=>{//
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.genreid= res.data.data
      //     this.formPush.fkShopId = res.data.data[0].id
      //   }
      // })
      // let org_tree={
      //   name:'',
      //   status:1
      // }
      // orgTree(org_tree).then((res)=>{//
      //   console.log(res)
      //   if(res.data.code == 200){
      //     this.dataTree = res.data.data
      //   }
      // })
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addhouellList(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = this.formUpdate
          updatehouellList(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getlist()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
        xqId:item.xqId,
        descDetail:item.descDetail,
        gmtCreate:item.gmtCreate,
        gmtVerify:item.gmtVerify,
        isVerify:item.isVerify,
        nickname:item.nickname,
        phone:item.phone,
        picture:item.picture,
        title:item.title,
        typeId:item.typeId,
        verifier:item.verifier,
      }

      this.updateDialog = true
      
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        houelldelectlist(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>

</style>



